.experience_container {
  /* margin-top: 30px; */

  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 8px;
}
.experience_title {
  display: block;
  /* margin-bottom: 8px; */
  font-size: 16px;
  font-weight: 600;
  /* margin: 6px 0; */
  color: #333;
  padding: 8px;
  height: 5rem;
}

.experience_scrollable_container {
  height: calc(100% - 10rem);
  overflow-y: scroll;
}

/* Hide scrollbar for WebKit-based browsers (Chrome, Safari) */
.experience_scrollable_container::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Optionally, you can style the thumb (dragging handle) */
.experience_scrollable_container::-webkit-scrollbar-thumb {
  background: transparent; /* This makes the thumb invisible */
}

.experience_footer {
  /* border: 1px solid rgb(53, 0, 128); */
  height: 5rem;
  /* background-color: aquamarine; */
}

.experience_fields {
  display: flex;
  flex-direction: column;
}

.experience_inputbox_devide {
  /* border: 1px solid green; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.experience_inputbox_devide .input-field{
    display: flex;
    width: calc(100% / 3 - 15px);
    flex-direction: column;
    margin: 4px 0;
}

.experience_inputbox_devide .input-field input{
    outline: none;
    font-size: 14px;
    font-weight: 400;
    color: #333;
    border-radius: 5px;
    border: 1px solid #aaa;
    padding: 0 15px;
    height: 42px;
    margin: 8px 4px;
}

.experience_fields .input-field span {
    font-size: 14px;
    font-weight: 500;
    color: #2e2e2e;
}

.experience_fields .input-field input:focus {
    border-color: #007bff; /* Change the border color to a shade of blue (#007bff) when focused */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Add a subtle box shadow when focused */
}

.textarea_inputbox{
    width: 100%;
}

.textarea_inputbox .input-field textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    resize: vertical;
    transition: border-color 0.3s ease;
  } 

.error-text {
  color: red;
  font-size: 14px;
  margin-top: 1px;
  height: 2rem;
}

@media (max-width: 821px) {
  .experience_fields {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 550px) {
  .experience_fields .input-field {
    width: 100%;
  }

  .experience_fields {
    display: flex;
    flex-direction: column;
  }
}
